<template>
  <div class="buyHouse">
    <navigation-bar  @onLeftClick="goBack" :navBarStyle="navStyle" leftImgType="1">
      <div class="nav-box" slot="nav-center">
        <div class="search" @click="search">
          <van-icon name="search" size="14px" color="#aaa"/>
          <input type="text" placeholder="请输入区域/小区名称" disabled v-model="keyWord">
        </div>
        <div class="city" @click="changeCity">
          <div class="city-name">{{cityName}}</div>
          <img :src="downIcon" alt="">
        </div>
      </div>
    </navigation-bar>
    <van-dropdown-menu active-color="#fb5768" v-if="!networkError">
      <van-dropdown-item :title="selectedCity ? selectedCity.NAME : '区域'" ref="city" @open="getCountry">
        <div class="city-range">
          <div class="right">
            <div class="city" :class="selectedCity ? '' : 'active'" @click="selectCity(null)">不限</div>
            <div v-for="(item, index) in cityRangeList" :key="index" class="city" :class="selectedCity && selectedCity.CODE == item.CODE ? 'active' : ''" @click="selectCity(item)">
              {{item.NAME}}
            </div>
          </div>
        </div>
      </van-dropdown-item>
      <van-dropdown-item :title="selectedPrice ? selectedPrice.value : '价格'" ref="price">
        <div class="price-range">
          <div class="price" :class="selectedPrice ? '' : 'active'" @click="selectPrice(null)">不限</div>
          <div v-for="(item, index) in priceRangeList" :key="index" class="price" :class="selectedPrice && selectedPrice.id == item.id ? 'active' : ''" @click="selectPrice(item)">
            {{item.value}}
          </div>
        </div>
        <div class="custom-range">
          <div class="title">自定义</div>
          <div>
            <input type="number" placeholder="最低价" v-model="price_start" @keyup="price_start.length == 1 ? price_start = price_start.replace(/[^1-9]/g,'') : price_start = price_start.replace(/\D/g,'')" @afterpaste="price_start.length == 1 ? price_start = price_start.replace(/[^1-9]/g,'') : price_start = price_start.replace(/\D/g,'')" @input="price_start.length > 5 ? price_start = price_start.slice(0,5) : ''">
          </div>
          <div class="title">—</div>
          <div>
            <input type="number" placeholder="最高价" v-model="price_end" @keyup="price_end.length == 1 ? price_end = price_end.replace(/[^1-9]/g,'') : price_end = price_end.replace(/\D/g,'')" @afterpaste="price_end.length == 1 ? price_end = price_end.replace(/[^1-9]/g,'') : price_end = price_end.replace(/\D/g,'')" @input="price_end.length > 5 ? price_end = price_end.slice(0,5) : ''">
          </div>
          <div class="confirm" @click="confirm">确定</div>
        </div>
      </van-dropdown-item>
      <van-dropdown-item :title="requestData.ROOM_CNT.length > 1 ? '多选' : (requestData.ROOM_CNT.length == 1 ? typeRangeList[requestData.ROOM_CNT[0] - 1].value : '户型')" ref="type">
        <div class="type-range">
          <div class="range">
            <van-checkbox-group v-model="selectedType">
              <van-cell-group>
                <van-cell clickable title="不限" @click="selectType([])"></van-cell>
                <van-cell
                  v-for="(item, index) in typeRangeList"
                  clickable
                  :key="index"
                  :title="item.value" @click="toggleType(index)"> 
                  <van-checkbox
                    :name="item.id + ''"
                    slot="right-icon"  
                    checked-color="#fb5768" 
                    shape="square"
                    ref="typeCheckboxs"
                  />
                </van-cell>
              </van-cell-group>
            </van-checkbox-group>
          </div>
          <div class="confirm" @click="confirmType">
            <div>确定</div>
          </div>
        </div>
      </van-dropdown-item>
      <van-dropdown-item :title="requestData.ACREAGE_LIST.length > 1 ? '多选' : (requestData.ACREAGE_LIST.length == 1 ? requestData.ACREAGE_LIST[0].ACREAGE_START + '-' + requestData.ACREAGE_LIST[0].ACREAGE_END + '平方米' : '面积')" ref="area">
        <div class="area-range">
          <div class="range">
            <van-checkbox-group v-model="selectedArea">
              <van-cell-group>
                <van-cell clickable title="不限" @click="selectArea([])"></van-cell>
                <van-cell
                  v-for="(item, index) in areaRangeList"
                  clickable
                  :key="index"
                  :title="item.value" @click="toggleArea(index)"> 
                  <van-checkbox
                    :name="item.id"
                    slot="right-icon"  
                    checked-color="#fb5768" 
                    shape="square"
                    ref="areaCheckboxs"
                  />
                </van-cell>
              </van-cell-group>
            </van-checkbox-group>
          </div>
          <div class="confirm" @click="confirmArea">
            <div>确定</div>
          </div>
        </div>
      </van-dropdown-item>
    </van-dropdown-menu>
    <div class="box" v-if="!networkError">
      <van-pull-refresh v-model="isRefresh" @refresh="refresh()">
        <div class="content-box" v-show ="houseList && houseList.length > 0">
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            :immediate-check="false"
            :offset="400"
            @load="loadMore">
            <div class="item" @click="goDetail(item.HOUSE_CODE)" v-for="(item, index) in houseList" :key="index">
              <div class="left">
                <img :src="item.PIC_URL ? item.PIC_URL : defaultIcon" alt=""  v-on:error.once="showErrorImg($event)">
              </div>
              <div class="middle">
                <div class="title">{{item.TITLE}}</div>
                <div class="bottom">
                  <div class="area">{{item.AREA}}</div>
                  <div class="room-box">
                    <div class="room">
                      <span>{{item.ROOM_HALL || ''}}</span>
                      <span>{{item.ACREAGE || ''}}m²</span>
                    </div>
                    <div class="price">{{item.PRICE}}万</div>
                  </div>
                </div>
              </div>
            </div>
          </van-list>
        </div>
        <div class="noData" v-show="houseList.length == 0 && !loading">
          暂无搜索结果！
        </div>
      </van-pull-refresh>
    </div> 
    <div style="height:300px;padding-top: 50px;" v-if="networkError">
      <NetworkError>
        <div slot="refreshMode" class="refresh-btn" @click="refresh()">
          重新加载
        </div>
      </NetworkError>
    </div> 
  </div>
</template>

<script>
import leftIcon from "@/assets/images/common/nav_return_white.png";
import downIcon from "@/assets/images/common/home_cutover_white.png";
import defaultIcon from "@/assets/images/home/houseRental/bannerHouse.jpg";
import {
  baseInfo,
  getHouseListService,
  getCountryService
} from "../../../services/houseRentalService";
import { store } from "../../../utils/store";
import NetworkError from "../../../common/networkError/NetworkError";
import NavigationBar from "@/common/currency/NavigationBar.vue";
export default {
  name: "buyHouse",
  data() {
    return {
      leftIcon: leftIcon,
      downIcon: downIcon,
      defaultIcon: defaultIcon,
      keyWord: "",
      houseList: [],
      loading: false,
      isRefresh: false,
      finished: false,
      cityName: "",
      cityCode: "",
      selectedPrice: null,
      selectedCity: null,
      selectedType: [],
      selectedArea: [],
      priceRangeList: [],
      cityRangeList: [],
      typeRangeList: [],
      areaRangeList: [],
      priceList: ["0", "100", "150", "200", "300", "500", null],
      areaList: ["0", "50", "100", "150", "200", "250", "9999"],
      price_start: "",
      price_end: "",
      requestData: {
        page: "1",
        rows: "10",
        HOUSE_FLAG: "0", //0二手房出售；1房屋出租)
        PROVINCE_CODE: "", //省代码
        CITY_CODE: "", //市代码
        COUNTY_CODE: "", //区代码
        PRICE_END: "", //价格结束
        PRICE_START: "", //价格开始
        ROOM_CNT: [], //几室
        ACREAGE_LIST: [], //面积
        KEYWDS: "" //关键字查询(包括:区域、售价、房型、校区名称)
      },
      networkError: false,
      navStyle: {
        background: "rgb(251, 87, 104)"
      }
    };
  },
  activated() {
    this.networkError = false;
    this.keyWord = this.$store.state.houseRental.areaName;
    this.requestData.KEYWDS = this.$store.state.houseRental.areaName;
    this.cityName = this.$store.state.houseRental.city.cityName
      ? this.$store.state.houseRental.city.cityName
      : this.$userInfo && this.$userInfo.CITY_NAME
        ? this.$userInfo.CITY_NAME
        : "青岛市";
    this.cityCode = this.$store.state.houseRental.city.cityCode;
    this.requestData.CITY_CODE = this.$store.state.houseRental.city.cityCode;
    this.requestData.CITY_NAME = this.$store.state.houseRental.city.cityName;
    if (this.$store.state.houseRental.reload) {
      this.reset();
      document.querySelector(".box").scrollTop = 0;
      this.$store.dispatch("setReload", false);
      this.priceRangeList = baseInfo.sales;
      this.typeRangeList = baseInfo.kinds;
      this.areaRangeList = baseInfo.sizes;
      this.getHouseList();
    } else {
      document.querySelector(".box").scrollTop = this.scrollTop;
    }
  },
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelector(".box").scrollTop;
    next();
  },
  components: {
    NetworkError: NetworkError,
    NavigationBar
  },
  methods: {
    showErrorImg: function(event) {
      event.currentTarget.src = this.defaultIcon;
    },

    /**
     * 页面返回事件
     */
    goBack() {
      this.reset();
      this.$router.go(-1);
    },

    /**
     * 重置数据
     */
    reset() {
      this.houseList = [];
      this.loading = false;
      this.isRefresh = false;
      this.finished = false;
      this.selectedPrice = null;
      this.selectedCity = null;
      this.selectedType = [];
      this.selectedArea = [];
      this.priceRangeList = [];
      this.cityRangeList = [];
      this.typeRangeList = [];
      this.areaRangeList = [];
      this.priceList = ["0", "100", "150", "200", "300", "500", null];
      this.areaList = ["0", "50", "100", "150", "200", "250", "9999"];
      this.price_start = "";
      this.price_end = "";
      this.requestData = {
        page: "1",
        rows: "10",
        HOUSE_FLAG: "0", //0二手房出售；1房屋出租)
        PROVINCE_CODE: "", //省代码
        CITY_CODE: this.requestData.CITY_CODE, //市代码
        CITY_NAME: this.requestData.CITY_NAME,
        COUNTY_CODE: "", //区代码
        PRICE_END: "", //价格结束
        PRICE_START: "", //价格开始
        ROOM_CNT: [], //几室
        ACREAGE_LIST: [], //面积
        KEYWDS: this.requestData.KEYWDS //关键字查询(包括:区域、售价、房型、校区名称)
      };
      this.scrollTop = 0;
    },

    /**
     * 获取房源列表
     */
    getHouseList() {
      this.loading = true;
      getHouseListService({ data: JSON.stringify(this.requestData) }).then(
        result => {
          this.networkError = false;
          this.setHouseList(result);
        },
        error => {
          if (this.requestData.page == "1") {
            this.networkError = true;
          }
          this.isRefresh = false;
          this.loading = false;
          this.finished = true;
          // this.$toast(error);
        }
      );
    },

    /**
     * 设置展示的工单列表信息
     */
    setHouseList(res) {
      this.loading = false;
      this.isRefresh = false;
      this.finished =
        res.COUNT <= this.requestData.page * this.requestData.rows;
      if (this.requestData.page == 1) {
        this.houseList = res.HOUSE_LIST ? res.HOUSE_LIST : [];
      } else {
        this.houseList = this.houseList.concat(
          res.HOUSE_LIST ? res.HOUSE_LIST : []
        );
      }
    },

    /**
     * 上拉加载更多
     */
    loadMore() {
      this.requestData.page = this.requestData.page * 1 + 1 + "";
      this.getHouseList();
    },

    /**
     * 下拉刷新
     */
    refresh: function() {
      this.isRefresh = true;
      this.resetRequestData();
      this.getHouseList();
    },

    /**
     * 重置请求数据
     */
    resetRequestData() {
      this.requestData = {
        page: "1",
        rows: "10",
        HOUSE_FLAG: "0", //0二手房出售；1房屋出租)
        PROVINCE_CODE: "", //省代码
        CITY_CODE: "", //市代码
        COUNTY_CODE: "", //区代码
        PRICE_END: "", //价格结束
        PRICE_START: "", //价格开始
        ROOM_CNT: [], //几室
        ACREAGE_LIST: [], //面积
        KEYWDS: "" //关键字查询(包括:区域、售价、房型、校区名称)
      };
      this.selectedType = [];
      this.selectedArea = [];
      this.selectedPrice = null;
      this.selectedCity = null;
      this.keyWord = "";
      this.price_start = "";
      this.price_end = "";
    },

    getCountry() {
      getCountryService({
        data: JSON.stringify({
          CITY_NAME: this.cityName || this.$userInfo.CITY_NAME
        })
      }).then(
        result => {
          this.cityRangeList = result;
        },
        error => {
          this.cityRangeList = [];
          this.$toast("获取区域失败");
        }
      );
    },

    /**
     * 价格点击事件
     */
    selectPrice(item) {
      this.requestData.page = "1";
      this.price_start = "";
      this.price_end = "";
      this.selectedPrice = item;
      if (item) {
        this.requestData.PRICE_START = this.priceList[item.id];
        this.requestData.PRICE_END = this.priceList[item.id + 1];
      } else {
        this.requestData.PRICE_START = "";
        this.requestData.PRICE_END = "";
      }
      this.$refs.price.toggle();
      this.getHouseList();
    },

    /**
     * 城市点击事件
     */
    selectCity(item) {
      this.requestData.page = "1";
      this.selectedCity = item;
      if (item) {
        this.requestData.COUNTY_CODE = item.CODE;
      } else {
        this.requestData.COUNTY_CODE = "";
      }
      this.$refs.city.toggle();
      this.getHouseList();
    },

    /**
     * 户型点击事件
     */
    selectType() {
      this.requestData.page = "1";
      this.requestData.ROOM_CNT = [];
      this.selectedType = [];
      this.$refs.type.toggle();
      this.getHouseList();
    },

    /**
     * 户型切换
     */
    toggleType(index) {
      this.$refs.typeCheckboxs[index].toggle();
    },

    /**
     * 确认不同户型的选择
     */
    confirmType() {
      this.requestData.page = "1";
      this.requestData.ROOM_CNT = this.selectedType;
      this.$refs.type.toggle();
      this.getHouseList();
    },

    /**
     * 面积点击事件
     */
    selectArea(item) {
      this.requestData.page = "1";
      this.requestData.ACREAGE_LIST = [];
      this.selectedArea = [];
      this.$refs.area.toggle();
      this.getHouseList();
    },

    /**
     * 面积切换
     */
    toggleArea(index) {
      this.$refs.areaCheckboxs[index].toggle();
    },

    /**
     * 确认不同面积的选择
     */
    confirmArea() {
      this.requestData.page = "1";
      this.requestData.ACREAGE_LIST = this.getRequestAreaList();
      this.$refs.area.toggle();
      this.getHouseList();
    },

    /**
     * 获取请求参数的面积数据
     */
    getRequestAreaList() {
      var list = [];
      for (var i = 0; i < this.selectedArea.length; i++) {
        var item = {};
        item.ACREAGE_START = this.areaList[this.selectedArea[i] - 1];
        item.ACREAGE_END = this.areaList[this.selectedArea[i]];
        list.push(item);
      }
      return list;
    },

    /**
     * 自定义价格
     */
    confirm() {
      if (
        this.price_start.length > 0 &&
        this.price_end.length > 0 &&
        this.price_start > this.price_end
      ) {
        this.$toast("最高价格不得小于最低价格");
        return;
      }
      if (this.price_start.length == 0 && this.price_end.length == 0) {
        this.$toast("查询条件不能为空");
        return;
      }
      this.requestData.page = "1";
      this.selectedPrice = {
        id: 10,
        value:
          this.price_end.length > 0
            ? this.price_start.length > 0
              ? this.price_start + "-" + this.price_end + "元"
              : this.price_end + "元以下"
            : this.price_start + "元以上"
      };
      this.requestData.PRICE_START =
        this.price_start.length > 0 ? this.price_start : "0";
      this.requestData.PRICE_END =
        this.price_end.length > 0 ? this.price_end : null;
      this.$refs.price.toggle();
      this.getHouseList();
    },

    /**
     * 搜索点击事件
     */
    search() {
      if (this.networkError) {
        return;
      }
      this.$router.push({
        name: "houseSearch",
        params: {
          cityName: this.cityName,
          cityCode: this.cityCode,
          keyWord: this.keyWord
        }
      });
    },

    /**
     * 城市点击事件
     */
    changeCity() {
      if (this.networkError) {
        return;
      }
      this.$router.push("houseCity");
    },

    /**
     * 跳转到详情页面
     */
    goDetail(HOUSE_CODE) {
      this.$router.push({
        name: "houseDetail",
        params: {
          HouseCode: HOUSE_CODE,
          cityName: this.cityName,
          cityCode: this.cityCode,
          keyWord: this.keyWord
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.buyHouse {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  .nav-box {
    width: 275px;
    margin: 0 auto;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    .search {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 174px;
      padding: 0 8px;
      box-sizing: border-box;
      height: 28px;
      border-radius: 14px;
      background: #ffffff;
      input {
        width: 130px;
        height: 20px;
        border: none;
        margin: 0;
        padding: 0;
        font-size: 14px;
        margin-left: 8px;
        outline: none;
        background: #ffffff;
        line-height: 20px;
      }
      input::-webkit-input-placeholder {
        color: #c9c9c9;
        font-size: 12px;
      }
    }
    .city {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-left: 8px;
      height: 44px;
      font-size: 13px;
      color: #fff;
      .city-name {
        max-width: 70px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      img {
        width: 8px;
        height: 4px;
        margin-left: 6px;
      }
    }
  }
  /deep/.van-ellipsis {
    font-size: 14px;
  }
  .city-range {
    width: 100%;
    height: 252px;
    font-size: 14px;
    .right {
      width: 100%;
      height: 252px;
      overflow: scroll;
      .city {
        height: 36px;
        line-height: 36px;
        text-align: center;
        border-bottom: 1px solid #fafafa;
      }
    }
    .active {
      color: #fb5768;
    }
  }
  .price-range {
    width: 100%;
    height: 204px;
    overflow: auto;
    .price {
      width: 100%;
      line-height: 20px;
      padding: 12px;
      box-sizing: border-box;
      font-size: 14px;
      text-align: center;
      border-bottom: 1px solid #fafafa;
    }
    .active {
      color: #fb5768;
    }
  }
  .custom-range {
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    input {
      width: 48px;
      height: 22px;
      border: none;
      margin: 12px;
      padding: 0;
      font-size: 12px;
      outline: none;
      background: #fff;
      text-align: center;
      line-height: 22px;
    }
    input::-webkit-input-placeholder {
      color: #c9c9c9;
      font-size: 12px;
    }
    .title {
      height: 24px;
      line-height: 24px;
    }
    .confirm {
      text-align: center;
      height: 24px;
      line-height: 24px;
      background: #fb5768;
      color: white;
      width: 48px;
      border-radius: 4px;
    }
  }
  .type-range,
  .area-range {
    height: 300px;
    width: 100%;
    .range {
      height: 252px;
      overflow: auto;
    }
    .confirm {
      height: 40px;
      padding-top: 8px;
      div {
        margin: 0 auto;
        text-align: center;
        width: 262px;
        border-radius: 4px;
        height: 32px;
        line-height: 32px;
        background: #fb5768;
        color: white;
        font-size: 14px;
      }
    }
    .active {
      color: #fb5768;
    }
  }
  .box {
    flex: 1;
    overflow: scroll;
    width: 100%;
    .noData {
      padding: 24px 12px;
      font-size: 14px;
      text-align: center;
    }
    .content-box {
      .item {
        width: 100%;
        height: 102px;
        padding: 12px;
        box-sizing: border-box;
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-top: 1px solid #f2f2f2;
        .left {
          width: 102px;
          height: 72px;
          img {
            width: 102px;
            height: 72px;
          }
        }
        .middle {
          width: 228px;
          margin-left: 20px;
          height: 78px;
          padding-right: 10px;
          box-sizing: border-box;
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          flex-direction: column;
          .title {
            width: 100%;
            font-size: 14px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
          }
          .bottom {
            width: 100%;
            .area {
              width: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              font-size: 14px;
              line-height: 20px;
              color: gray;
            }
            .room-box {
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: center;
